<template>
    <div>
      <!-- Page header -->
      <section class="page-action action-margin tablehead mobile_d-none">
        <div class="col-md-12 p-0">
          <div class="theme-primary partition p-actions-expand">
            <span
              id="titleName"
              class="p-name text-white"
              style="text-transform: capitalize;"
            >
              {{ $t('Dashboard') }}
            </span>
          </div>
        </div>
      </section>

      <div class="col-12 mb-2 mt-2">
                    <div class="mr-2 d-inline-block mt-2">
                        <span class="legend-Progress colorboxsquare"></span> Invoice(s) generated from milestone
                    </div>
                    <div class="input-group col-lg-6 col-xl-4 float-right p-0">
                        <input type="hidden" id="hdnNotifyMsg">
                        <div id="divAccrualNotification" class="col-11 d-none text-danger p-2 mt-5">
                        </div>
                        <label class="col-form-label">{{$t('ChooseCompany')}}:</label>
                        <div class="input-group form-control p-0 border-0">
                            <input type="hidden" id="hdnSearch" value="">
                            <select
                                v-model="selectedCompany"
                                @change="onCompanyChange"
                                class="form-control"
                                id="ddlcompanylist"
                                tabindex="1"
                                >
                                <option disabled value="">
                                    {{ $t('SelectCompany') }}
                                </option>
                                <option
                                    v-for="company in companyListData"
                                    :key="company.value"
                                    :value="company.value"
                                >
                                    {{ company.name }}
                                </option>
                                </select>


                            <div class="input-group-append">
                                <RouterLink class="input-group-text" to="/Client/CompanyListing" id="basic-addon2"><a title="" class="text-dark"
                                      data-toggle-tooltip="tooltip" data-original-title="View List of All Companies">
                                    <i class="fa fa-eye"></i></a></RouterLink>
                            </div>
                        </div>
                    </div>
                </div>
  
      <!-- Dashboard Widgets -->
      <div class="mx-3">
        <ul class="dashbord-widget py-3">
          <li class="cursor-pointer one_wid" style="cursor: pointer !important;">
            <h3 style="width: 65%;">
              {{ billAmountData.amount_due == null ? 0 : billAmountData.amount_due }}
              <br />
              <span>{{$t('Billamountdue')}}</span>
            </h3>
            <i aria-hidden="true" class="fa fa-dollar"></i>
          </li>
          <li class="cursor-pointer two_wid" style="cursor: pointer">
            <h3>
              {{ billAmountData.amount_paid == null ? 0 : billAmountData.amount_paid }}
              <br />
              <span>{{ $t('Lastpaidbillamount') }}</span>
            </h3>
            <i aria-hidden="true" class="fa fa-dollar"></i>
          </li>
        </ul>
      </div>
  
      <!-- Charts and Widgets -->
      <div>
        <div class="row">
          <!-- Monthly Billable Amount with empty bars -->
          <div class="col-md-6 pb-3">
            <div class="graph-widget-box">
              <div class="graph-widget-heading">{{ $t('MonthlyBillableAmount') }}</div>
              <VueApexCharts  class="VueApexCharts_set_width_height" type="bar" :options="MonthlyBillable" :series="MonthlyBillable.series" />
             
            </div>
          </div>
  
          <!-- Monthly Consumption with filled bars -->
          <div class="col-md-6 pb-3">
            <div class="graph-widget-box">
              <div class="graph-widget-heading">{{ $t('MonthlyConsumption') }}</div>
  
              <VueApexCharts class="VueApexCharts_set_width_height" type="bar" :options="MonthlyConsumption" :series="MonthlyConsumption.series" />
            
            </div>
          </div>
        </div>
  
        <!-- Recent Invoices -->
        <div class="row">

  <!-- Recent Invoices Table -->
<div class="col-md-6 pb-3">
  <div class="graph-widget-box">
    <div class="graph-widget-heading">{{ $t('RecentInvoices') }}</div>
    <div class="graph-widget-content table-responsive" style="max-height: 400px; overflow-y: auto;" v-if="invoiceData.length > 0">
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>#</th>
            <th>{{ $t('Header_Projects') }}</th>
            <th>{{ $t('IssueDate') }}</th>
            <th>{{$t('DueDate')}}</th>
            <th>{{ $t('Amount') }}</th>
            <th>{{ $t('Status_MyDashboard_Module') }}</th>
            <th>{{ $t('View') }}</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(invoice, index) in invoiceData" :key="invoice.BILL_ID">
            <td>{{ index + 1 }}</td>
            <td>{{ invoice.PROJECT_NAME || '--' }}</td>
            <td>{{ $d(invoice.CREATED_AT,"short") }}</td>
            <td>{{ $d(invoice.BILL_DUE_DATE,"short") }}</td>
            <td>{{ invoice.BILL_NET_TOTAL.toLocaleString() }} ({{ invoice.ISO_CODE }})</td>
            <td>{{ invoice.STATUS_NAME }}</td>
            <td>
              <a  class="text-primary" title="View Invoice" @click="ViewInvoice(invoice)">
                <i class="fa fa-eye"></i>
              </a>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="text-center text-danger" v-else>
          {{ $t('NoRecordfound') }}
    </div>
  </div>
</div>

          <!-- Open Tickets -->
          <div class="col-md-6 pb-3">
            <div class="graph-widget-box">
  <div class="graph-widget-heading">{{ $t('OpenTickets') }}</div>
  <div class="graph-widget-content" id="OpenTicketWidget" style="max-height: 300px; overflow-y: auto;"  v-if="ticketData.open_ticket_details.length > 0">
    
    <!-- Ticket Table -->
    <table class="table table-striped table-bordered mb-0">
      <thead class="thead-dark">
        <tr>
          <th class="text-center">#</th>
          <th class="text-center">{{ $t('TicketNo') }}</th>
          <th class="text-center">{{ $t('Subject') }}</th>
          <th class="text-center">{{ $t('Status_MyDashboard_Module') }}</th>
        </tr>
      </thead>
      <tbody>
    
        <tr v-for="(ticket, index) in ticketData.open_ticket_details" :key="ticket.TICKET_NUMBER">
          <td class="text-center">{{ index + 1 }}</td>
          <td class="text-center">{{ ticket.TICKET_NUMBER }}</td>
          <td class="text-center">{{ ticket.TICKET_SUBJECT }}</td>
          <td class="text-center">{{ ticket.STATUS_NAME }}</td>
        </tr>
      </tbody>
    </table>

  </div>
    <div class="text-center text-danger" v-else>
          <div >  {{ $t('NoRecordfound') }}</div>
        </div>
</div>

</div>

        </div>
        <!-- open model for details -->
        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="invoiceModel">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('Invoice') }}</h4>
                                <button type="button" class="close" v-on:click="CloseInvoiceModel">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <div class="table-responsive">
  <table class="table table-striped table-bordered">
    <thead class="thead-dark">
      <tr>
        <th>{{ $t('Header_Invoice') }} #</th>
        <th>{{ $t('Client_Billing') }}</th>
        <th>{{$t('DueDate')}}</th>
        <th>{{ $t('Status_MyDashboard_Module') }}</th>
        <th>{{ $t('Amount') }}</th>
        <th>{{ $t('Company') }}</th>
        <th>{{ $t('Plan') }}</th>
      </tr>
    </thead>
    <tbody>
      <tr>
        <td>{{ invoiceModelData.BILL_NUMBER }}</td>
        <td>{{ invoiceModelData.CLIENT_NAME }}</td>
        <td>{{ $d(invoiceModelData.BILL_DUE_DATE,"short") }}</td>
        <td>{{ invoiceModelData.STATUS_NAME }}</td>
        <td>{{ invoiceModelData.BILL_NET_TOTAL }} {{ invoiceModelData.ISO_CODE }}</td>
        <td>{{ invoiceModelData.COMPANY_NAME }}</td>
        <td>{{ invoiceModelData.PLAN_NAME }}</td>
      </tr>
    </tbody>
  </table>
</div>


                            </div>
                            </div>
                    </div>
                </div>
            </div>
        </transition>
      </div>
    </div>
  </template>
  
  <script>
  import DataService from '../../services/DataService';
  import VueApexCharts from 'vue-apexcharts';
  export default {
    components: {
     VueApexCharts
  },
  data() {
    return {
        invoiceModel:false,
        invoiceModelData:'',
        companyListData:[],
        selectedCompany:'',
        DateColumn:[],
        UserId:0,
        pageSize: 10,
        pageNum: 1,
        sortBy: "",
        searchCondition: "",
        sortExp: "",
        checkPrivilegeaccess:'',
        invoiceData:'',
        billAmountData: '',
        ticketData:'',
        consumptionData:'',
        companyList:'',
        //use for monthly Bill
        MonthlyBillable: {
            series:[{
            name:'Bill Amount',
            data:[1000,2000,3000,4000,5000,6000,7000],

        }], 
        chart: {   toolbar: {show: false}},
        xaxis: {
          categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
          labels: {style: {fontSize: '14px'}},
          axisBorder: { show: true, color: '#999' },
          axisTicks: { show: true, color: '#999', height: 6 }
        },
        grid: {  xaxis: { lines: { show: true } },yaxis: { lines: { show: true } }},
        yaxis: {min: 0,max: 9000,tickAmount: 9,
          labels: {style: {fontSize: '14px'}}
        },
        plotOptions: {bar: {horizontal: false,borderRadius: 4,
            dataLabels: {position: 'top' }}
        },dataLabels: {enabled: false },
        tooltip: { enabled: true },
        legend: {show: false},
        title:{ text: 'Bill Amount',align: 'center',}
      },
    //   use for Monthly Consumption
      MonthlyConsumption: {
        series:[{
            name:'Consumption',
          data: []
        }], 
        chart: {   toolbar: {show: false}},
        xaxis: {
          categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
          labels: {style: {fontSize: '14px'}},
          axisBorder: { show: true, color: '#999' },
          axisTicks: { show: true, color: '#999', height: 6 }
        },
         grid: {xaxis: { lines: { show: true } },yaxis: { lines: { show: true } }},

        yaxis: {min: 0.1,max: 1.0,tickAmount: 9,
          labels: {style: { fontSize: '14px'}}
        },
        plotOptions: {
          bar: {
            horizontal: false,
            borderRadius: 4,
            dataLabels: {
              position: 'top' 
            }
          }
        },
        dataLabels: {enabled: false },
        tooltip: {enabled: true },
        legend: {show: false},
        title:{text: 'Consumption',align: 'center', }
      }, 
    };
    },
  
   async created() { 
    this.UserId = this.GetUserInfo.ID;
    var company = this.selectedCompany === "" ? this.GetUserInfo.CompanyId : this.selectedCompany;
       await this.GetClientWidget(company);
       await this.GetTicket(company);
       await this.GetConsumption(company);
       await this.getinvoice(company);
      this.GetCompanyDDlData();
      this.selectedCompany=company;
      this.CheckPrivilege();
      this.updateChart();
    },
  
 
  
    methods: {
        async getinvoice(CId) {
      const url = `companyId=${CId}&pageSize=${this.pageSize}&pageNum=${this.pageNum}&sortBy=${this.sortBy}&sortExp=${this.sortExp}&clientId=${this.UserId}`;
            await DataService.GetDashboardInvoiceListing(url).then((response) => {
                if (response.data) {
                    this.invoiceData = response.data;
                    this.invoiceData.map((element)=>{
                        if(element.BILL_DUE_DATE || element.CREATED_AT )
                    {
                        element.BILL_DUE_DATE=new Date(element.BILL_DUE_DATE);
                        element.CREATED_AT=new Date(element.CREATED_AT);
                    }
                    if (element.STATUS_NAME === "Sent") {
                    const total = element.BILL_NET_TOTAL;
                    if (total < 0) {
                        element.STATUS_NAME = "Paid";
                    } else if (total === 0) {
                        element.STATUS_NAME = "Bill Generated";
                    } else {
                        element.STATUS_NAME = "UnPaid";
                    }
                }

                return element;
                    })
                }
            });
            },
           async onCompanyChange()
            {
                debugger
               var company = this.selectedCompany === "" ? this.GetUserInfo.CompanyId : this.selectedCompany;
                await this.GetClientWidget(company);
                await this.GetTicket(company);
                await this.GetConsumption(company);
                await this.getinvoice(company);
            },
      
         ViewInvoice(data){
                this.invoiceModel=true;
                this.invoiceModelData=data;
          },
        CloseInvoiceModel()
                {
                    this.invoiceModel=false
            },
        async  GetClientWidget(CId) {
            const url = `Code=${'AMOUNT_WIDGET'}&companyId=${CId}`;
        await  DataService.GetDashboardWidgetData(url).then((response) => {
            debugger
            if (response.data) {
                this.billAmountData =JSON.parse(response.data[0].Column1);
            }
            });
        },
        async  GetTicket(CId) {
            const url = `Code=${'OPEN_TICKETS'}&companyId=${CId}`;
            await  DataService.GetDashboardWidgetData(url).then((response) => {
            if (response.data) {
                this.ticketData =JSON.parse(response.data[0].Column1);
            }
            });
        },
        async  GetConsumption(CId) {
            const url = `Code=${'CONSUMPTION'}&companyId=${CId}`;
            await  DataService.GetDashboardWidgetData(url).then((response) => {     
            if (response.data) {
                this.consumptionData =JSON.parse(response.data[0].Column1);
            }
            });
        },
       CheckPrivilege()
       {
        var param = 'controller=Client&action=Index';
        DataService.CheckPrivilege(param).then(function (response) {
         if (response.data===true)  {
           this.checkPrivilegeaccess =true;      
          }   }.bind(this))
       },
       GetCompanyDDlData() {
  const vm = this;
  const url = `id=&moduleName=&type=GET_COMPANY_LIST&search=&refId=`;

  DataService.GetDDLData(url).then((response) => {
    if (response.data && response.data.DATA && response.data.DATA.length > 0) {
      vm.companyListData = response.data.DATA;
    } else {
      vm.companyListData = [];
      vm.selectedCompany = ""; 
    }
  });
},

      updateChart() {
  const fullMonthList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
  const monthDataMap = {};
  this.billAmountData.amount_details.forEach((data) => {
    monthDataMap[data.MONTH] = data.AMOUNT;
  });
  const amounts = [];
  for (let i = 1; i <= 12; i++) {
    amounts.push(monthDataMap[i] || 0);
  }
  const maxAmount = Math.max(...amounts);
  const roundedMax = Math.ceil(maxAmount / 10000) * 10000 || 10000; 
  this.MonthlyBillable = {
    ...this.MonthlyBillable,
    series: [{
      name: 'Bill Amount',
      data: amounts
    }],
    xaxis: {
      ...this.MonthlyBillable.xaxis,
      categories: fullMonthList
    },
    yaxis: {
      min: 0,
      max: roundedMax,
      tickAmount: 10,
      labels: {
        style: { fontSize: '14px' }
      }
    },
    tooltip: {
      enabled: true,
      y: {
        formatter: (val) => '$' + val.toLocaleString()
      }
    }
  };

  // Update MonthlyConsumption chart
//   this.MonthlyConsumption.series = [{
//     name: 'Consumption',
//   }];
this.consumptionData.consumption_details.forEach((data) => {
  const month = parseInt(data.MONTH);
  const amount = parseFloat(data.AMOUNT); 
  consumptionMap[month] = isNaN(amount) ? 0 : amount;
});
const consumptionAmounts = [];
for (let i = 1; i <= 12; i++) {
  consumptionAmounts.push(consumptionMap[i] || 0);
}

this.MonthlyConsumption = {
  ...this.MonthlyConsumption,
  series: [{
    name: 'Consumption',
    data: consumptionAmounts
  }],
  xaxis: {
    ...this.MonthlyConsumption.xaxis,
    categories: fullMonthList
  },
  tooltip: {
    enabled: true,
    y: {
      formatter: (val) => val.toFixed(2) 
    }
  },
  yaxis: {
    min: 0,
    max: Math.max(...consumptionAmounts) > 0
      ? Math.ceil(Math.max(...consumptionAmounts) * 1.2) 
      : 1,
    tickAmount: 10,
    labels: {
      style: { fontSize: '14px' }
    }

  }
}
      }
    }
}
  </script>
  <style>
  .table td, .table th {
  vertical-align: middle;
  text-align: center;
}
.graph-widget-content table {
  font-size: 14px;
}
.thead-dark th {
  background-color: #343a40;
  color: white;
}
.VueApexCharts_set_width_height{
    width:920 !important;
    height:460 !important
}
</style>
 
  